import { useState, useEffect } from 'react';
import SwipeableViews from 'react-swipeable-views';
import { Box, Typography, Radio, Stack, Button } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';

const ImageGallery = () => {
  const imageData = [
    {
      src: 'https://source.unsplash.com/random?1',
      category: 'Category 1',
      description: 'Description for Category 1',
    },
    {
      src: 'https://source.unsplash.com/random?2',
      category: 'Category 2',
      description: 'Description for Category 2',
    },
    {
      src: 'https://source.unsplash.com/random?3',
      category: 'Category 3',
      description: 'Description for Category 3',
    },
    // Add more image data as needed
  ];

  const [activeStep, setActiveStep] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setActiveStep((prevActiveStep) => (prevActiveStep + 1) % imageData.length);
    }, 3000); // Change slide duration as needed (in milliseconds)

    return () => clearInterval(interval);
  }, [activeStep, imageData.length]);

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  return (
    <Box sx={{ position: 'relative', width: '100%', height: 500, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
      <SwipeableViews index={activeStep} onChangeIndex={handleStepChange}>
        {imageData.map((slide, index) => (
          <Box key={index} style={{ position: 'relative' }}>
            <img src={slide.src} alt={`Slide ${index}`} style={{ width: '100%', height: '500px', objectFit: 'cover' }} />
            <Box sx={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', textAlign: 'center', color: 'white', padding: '20px' }}>
              <Typography variant="h4">{slide.category}</Typography>
              <Typography variant="subtitle1">{slide.description}</Typography>
            </Box>
          </Box>
        ))}
      </SwipeableViews>

      <Box sx={{ position: 'absolute', bottom: '50px' }}>
        <Button variant="contained" color="primary">
          Shop Now
        </Button>
      </Box>

      <Stack sx={{ position: 'absolute', bottom: 0, width: '100%', justifyContent: 'center' }} direction="row" spacing={1}>
        {imageData.map((_, index) => (
          <Radio
            key={index}
            color="primary"
            checked={activeStep === index}
            onChange={() => handleStepChange(index)}
          />
        ))}
      </Stack>

      <ArrowBackIcon onClick={() => handleStepChange((activeStep - 1 + imageData.length) % imageData.length)} />
      <ArrowForwardIcon onClick={() => handleStepChange((activeStep + 1) % imageData.length)} />
    </Box>
  );
};

export default ImageGallery;
