import * as React from 'react';
import PropTypes from 'prop-types';
import { AppBar, Toolbar, Typography, IconButton, Link, Box } from '@mui/material';
import { Favorite, ShoppingBasket, AccountCircle, SearchIcon } from '@mui/icons-material';
import VerticalModalSignIn from '../../pages/authentication/login_dialog';
import { handleOpen } from '../../store/loginModalReducer';
import { useDispatch } from 'react-redux';

function Header(props) {
  const { sections, title, subtitle } = props;
  const dispatch = useDispatch();

  return (
    <React.Fragment>
      <Toolbar sx={{ borderBottom: 1, borderColor: 'divider' }}>
        {/* Left side - Logo */}
        <a href="#">
        <img src={require("../../assets/img/logo_1.png")} alt="Logo" style={{ width: '60px', marginRight: '10px' }} />
        </a>

        {/* Center - Heading and Subheading */}
        <div style={{ flex: 1, textAlign: 'center' }}>
          <Typography variant="h4">
            <span style={{ fontFamily:'Afterglow-Regular', fontSize:'50px' }}>Morail</span>
            <span style={{ fontFamily: 'Lucian-CAT' }}>'S</span>
          </Typography>
          <Typography variant="subtitle1" sx={{fontFamily: 'jane-austen', fontSize:'25px'}}>{subtitle}</Typography>
        </div>

        {/* Right side - Icons */}
        <IconButton color="inherit">
          <Favorite />
        </IconButton>
        <IconButton color="inherit">
          <ShoppingBasket />
        </IconButton>
        <IconButton color="inherit">
          <AccountCircle onClick={()=> dispatch(handleOpen()) } />
          <VerticalModalSignIn />
        </IconButton>
      </Toolbar>
      <Toolbar
        component="nav"
        variant="dense"
        sx={{ justifyContent: 'center', overflowX: 'auto' }}
      >
        {sections.map((section) => (
          <Link
            color="#d9af59"
            noWrap
            key={section}
            variant="body2"
            href={section?.url}
            sx={{
              p: 1, 
              flexShrink: 0,   
              textTransform: 'uppercase', 
              textDecoration: 'none',
              '&:hover': {
                cursor: 'pointer',
              },
            }}
          >
            {section}
          </Link>
        ))}
      </Toolbar>
    </React.Fragment>
  );
}

Header.propTypes = {
  sections: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      url: PropTypes.string.isRequired,
    }),
  ).isRequired,
  title: PropTypes.string.isRequired,
};

export default Header;