import React from "react";
import {
  Card,
  CardMedia,
  CardContent,
  Typography,
  Box,
  IconButton,
  Chip,
} from "@mui/material";
import SwipeableViews from "react-swipeable-views";
import NavigateBeforeIcon from "@mui/icons-material/NavigateBefore";
import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import { Favorite } from "@mui/icons-material";

// const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const CarouselCard = (props) => {
  const { product, newTag, inWish } = props;
  const [activeStep, setActiveStep] = React.useState(0);

  const maxSteps = product?.images?.length;

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  const handleNext = () => {
    setActiveStep((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
  };

  const handleBack = () => {
    setActiveStep(
      (prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps
    );
  };

  return (
    <Card elevation={3}>
      <Box sx={{ position: "relative" }}>
        <SwipeableViews index={activeStep} onChangeIndex={handleStepChange}>
        {product?.images.map((image, index) => (
            <Box key={index} sx={{ position: 'relative', overflow: 'hidden',  }}>
              {Math.abs(activeStep - index) <= 2 ? (
                <CardMedia
                  component="img"
                  height="400"
                  image={image}
                  alt={`Image ${index + 1}`}
                  sx={{
                    transition: 'transform 0.3s', // Add a smooth transition for the transform effect
                    '&:hover': {
                      transform: 'scale(1.1)', // Apply the scale transformation on hover
                      overflow: 'hidden', // Hide the scrollbar on hover
                    },
                    '&::-webkit-scrollbar': {
                      display: 'none', // Hide the scrollbar for webkit-based browsers
                    },
                  }}
                />
              ) : null}
              {index === activeStep && (
                <React.Fragment>
                <Box sx={{ position: 'absolute', top: 0, right: 0, padding: '10px' }}>
                  <IconButton aria-label="add to favorites">
                    <Favorite sx={{color: inWish ? 'red' : 'primary'}}/>
                  </IconButton>
                </Box>
                {newTag && (
                    <Box
                    sx={{
                      position: 'absolute',
                      top: '10px',
                      left: '10px',
                      bgcolor: 'rgba(0,0,0,0.6)',
                      color: 'white',
                      padding: '5px',
                      borderRadius: '5px',
                      bgcolor:'#1d2547'
                    }}
                  >
                    <Typography variant="body2">New</Typography>
                  </Box>
                )}
                
               </React.Fragment> 
              )}
            </Box>
          ))}
        </SwipeableViews>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: 0,
            right: 0,
            transform: "translateY(-50%)",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            padding: "10px",
            pointerEvents: "false", // To enable clicks on the image
          }}
        >
          <IconButton onClick={handleBack} disabled={activeStep === 0}>
            <NavigateBeforeIcon />
          </IconButton>
          <IconButton
            onClick={handleNext}
            disabled={activeStep === maxSteps - 1}
          >
            <NavigateNextIcon />
          </IconButton>
        </Box>
      </Box>
      <CardContent>
        <Typography gutterBottom variant="h5" component="h2">
          {product.name}
        </Typography>
        <Typography variant="body1" color="textSecondary">
          Price: {product.price}
        </Typography>
        <Box sx={{ display: "flex", alignItems: "center", flexWrap: "wrap" }}>
          <Typography variant="body2" sx={{ marginRight: 1 }}>
            Available Colors:
          </Typography>
          {product.colors.map((color, index) => (
            <Box
              key={index}
              sx={{
                width: 20,
                height: 20,
                borderRadius: "50%",
                backgroundColor: color,
                marginRight: 2,
                marginTop: 2,
              }}
              title={color}
            />
          ))}
        </Box>
        <Typography variant="body2" color="textSecondary" gutterBottom>
          Available Sizes:
          {product.sizes.map((size, index) => (
            <Chip key={index} label={size} style={{ marginRight: 5 }} />
          ))}
        </Typography>
      </CardContent>
    </Card>
  );
};

export default CarouselCard;
